  width: 40px;
}

.socialMedia a:hover img {
  opacity: 0.7;
}
.flexCenter {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

div.socialMedia {
  display: flex;
  min-width: 100px;
  border-radius: 5px;
  justify-content: space-between;
}
.socialMedia img {
  -webkit-filter: invert(1);
  filter: invert(1);
}
#actionCall {
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-name: bounce;
          animation-name: bounce;
  -webkit-animation-iteration-count: 18;
          animation-iteration-count: 18;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-filter: invert(1);
  filter: invert(1);
}
@-webkit-keyframes bounce {
  0% {
    -webkit-transform: scale(1, 1) translateY(0);
            transform: scale(1, 1) translateY(0);
  }
  10% {
    -webkit-transform: scale(1.1, 0.9) translateY(0);
            transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    -webkit-transform: scale(0.8, 1.2) translateY(-20px);
            transform: scale(0.8, 1.2) translateY(-20px);
  }
  60% {
    -webkit-transform: scale(1.5, 0.5) translateY(0px);
            transform: scale(1.5, 0.5) translateY(0px);
  }
  70% {
    -webkit-transform: scale(1.4, 0.7) translateY(0px);
            transform: scale(1.4, 0.7) translateY(0px);
  }
  95% {
    -webkit-transform: scale(1.1, 0.9) translateY(0px);
            transform: scale(1.1, 0.9) translateY(0px);
  }
  100% {
    -webkit-transform: scale(1, 1) translateY(0);
            transform: scale(1, 1) translateY(0);
  }
}
@keyframes bounce {
  0% {
    -webkit-transform: scale(1, 1) translateY(0);
            transform: scale(1, 1) translateY(0);
  }
  10% {
    -webkit-transform: scale(1.1, 0.9) translateY(0);
            transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    -webkit-transform: scale(0.8, 1.2) translateY(-20px);
            transform: scale(0.8, 1.2) translateY(-20px);
  }
  60% {
    -webkit-transform: scale(1.5, 0.5) translateY(0px);
            transform: scale(1.5, 0.5) translateY(0px);
  }
  70% {
    -webkit-transform: scale(1.4, 0.7) translateY(0px);
            transform: scale(1.4, 0.7) translateY(0px);
  }
  95% {
    -webkit-transform: scale(1.1, 0.9) translateY(0px);
            transform: scale(1.1, 0.9) translateY(0px);
  }
  100% {
    -webkit-transform: scale(1, 1) translateY(0);
            transform: scale(1, 1) translateY(0);
  }
}

